.service {
  margin-bottom: 60px;

  &__wrapper {
    width: var(--content-width);
    margin: 0 auto;
  }

  &__title {
    color: #000;
    font-size: 36px;
    font-weight: 400;
    line-height: normal;
    margin: 0 0 25px;

    @media screen and (max-width: 767px) {
      font-size: 24px;
      margin-bottom: 20px;
    }
  }

  &__content {
    width: 100%;
    height: 500px;
    border-radius: 10px;
    border: 1px solid #000;
    overflow: hidden;
    display: flex;

    @media screen and (max-width: 1439px) {
      height: 437px;
    }

    @media screen and (max-width: 1023px) {
      flex-direction: column;
      height: unset;
    }
  }

  &__img {
    height: 100%;
    width: 775px;

    @media screen and (max-width: 1439px) {
      width: 582px;
    }

    @media screen and (max-width: 1023px) {
      width: 100%;
      height: calc(100vw * .56);
    }
  }

  &__swiper {
    flex-grow: 1;
    height: 100%;
    position: relative;

    @media screen and (max-width: 1023px) {
      height: unset;
      flex-grow: unset;
      width: 100%;
    }
  }

  &__swiper-wrapper {
    @include reset-list;
    width: 100%;
    height: 100%;
  }

  &__swiper-slide {
    box-sizing: border-box;
    padding: 38px 0 0 74px;
    display: flex;
    flex-direction: column;
    gap: 30px;

    @media screen and (max-width: 1439px) {
      padding-top: 20px;
      padding-left: 28px;
      gap: 25px;
    }

    @media screen and (max-width: 1023px) {
      padding-left: 176px;
    }

    @media screen and (max-width: 767px) {
      padding-left: 20px;
      gap: 20px;
    }
  }

  &__swiper-title {
    color: #000;
    font-family: Gilroy;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 150%;
    margin: 0;

    @media screen and (max-width: 767px) {
      font-size: 20px;
    }
  }

  &__swiper-list {
    @include reset-list;
    display: flex;
    flex-direction: column;
    gap: 10px;
    list-style: disc;
    width: 367px;
    box-sizing: border-box;
    padding-left: 15px;
    margin-left: 10px;

    @media screen and (max-width: 1439px) {
      gap: 8px;
    }

    @media screen and (max-width: 767px) {
      width: 96%;
      margin-left: 0;
    }
  }

  &__swiper-item {
    color: #000;
    font-size: 20px;
    font-weight: 400;
    line-height: 150%;

    @media screen and (max-width: 767px) {
      font-size: 16px;
    }
  }

  &__swiper-pagination {
    display: flex;
    gap: 20px;
    position: absolute;
    top: unset !important;
    left: unset !important;
    bottom: 40px !important;
    right: 50% !important;
    width: unset !important;
    transform: translateX(50%) !important;

    @media screen and (max-width: 1439px) {
      bottom: 20px !important;
    }

    @media screen and (max-width: 1023px) {
      position: relative;
      bottom: unset !important;
      left: unset !important;
      transform: translateX(0) !important;
      right: unset !important;
      margin: 38px auto 15px;
      justify-content: center;
    }

    .swiper-pagination-bullet {
      opacity: 1;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      border: 1px solid #000;
      background-color: transparent;
      @include transition;
      margin: 0 !important;

      @media screen and (max-width: 767px) {
        width: 11px;
        height: 11px;
      }
    }

    .swiper-pagination-bullet-active {
      background-color: #000;
    }
  }
}